//
// forest
//
#forest{
	width:100vw;
	.share {
		padding: rem(30px) 0 rem(100px);
	}
	.forestLocation {
		width:100vw;
		height:calc(100vw * 647 / 640);
		background:#f2f1f1 url('../img/forest/map.jpg') no-repeat 0% 50%/contain;
		.location_desc {
			position:relative;
			height:calc(100vw * 647 / 640);	
			.map_point {
				position:absolute;
				&.map_point1 {
					top:30%;
					left:28%;
				}
				&.map_point2 {
					top:50%;
					left:28%;
				}
				&.map_point3 {
					top:52%;
					left:75%;
				}
				&.map_point4 {
					top:55%;
					left:50%;
				}
				&.map_point5 {
					top:60%;
					left:80%;
				}
				&.map_point6 {
					top:75%;
					left:40%;
				}
				.pin {
					width: rem(15px);
					height: rem(15px);
					background: url('data:img/png;base64,') no-repeat 50% 50%/cover;
					position: absolute;
					z-index:10;
				}
			.desc {
				border-radius:10px;
				background-color: $orange;	
				width: rem(190px);
				// height: rem(40px);
				opacity: 0;
				visibility: hidden;
				position: absolute;
				left: rem(-110px);
				&:before {
					background-color: $orange;
					width: 1px;
					height: rem(36px);
					content:'';
					display: block;
					position:absolute;
					left:50%;
					margin-left: rem(22px);
					bottom: rem(-36px);
					z-index:1; 
				}
				.numText {
					width: rem(30px);
					color:$text-color-white;
					font-size: rem(16px);
					line-height: rem(20px);
					display: inline-block;
					text-align:center;
					i {
						font-size:rem(9px);
						font-style:normal;
					}
				}
				.text {
					width: rem(150px);
					height: rem(30px);
					color:$text-color-gray;
					background-color: $text-color-white;
					border-radius: 10px;
					display: inline-block;
					padding: rem(10px) 0;
					margin: rem(5px) rem(5px) rem(5px) 0;
					font-size:rem(11px);
					span {
						position:relative;
						font-size: rem(10px);
						letter-spacing:-1px;
						font-weight:300;	
						.m2 {
							position:absolute;
							font-weight:400;
							font-size:rem(5px);
							top:rem(-3px);
						}
					}
				}		
				.tree {
					position:absolute;
					top:0;
					left:50%;
					margin-left: rem(-82.5px);
					background: url('data:img/png;base64,') no-repeat 50% 50%/cover;
					width:rem(165px);
					height:rem(64px);
					opacity: 0;
					visibility: hidden;
				}
			}
		}
	}
}
		.map_desc {
			width: 100vw;
			height: 100%;
			padding: rem(50px) 0 rem(30px);
			.section-header {
				.heading {
					color: $text-color-gray;
					text-align: center;
					font-size: rem(24px);
					.text-color-orange {
						font-weight: 700;
					}
				}
				.btn-corner-rounded {
					width: rem(280px);
					margin: rem(20px) auto;
					display:block;
				}
			}
			.owl-carousel {
				width:90vw;
				margin:0 auto;
				.img {
					width: 90vw;
					height: calc(90vw * 290 / 575);
					display:block;
					border-radius:10px;
				}
				.desc {
					padding-top: rem(50px);
					color: $text-color-gray;
					text-align: center;
					font-size: rem(14px);
					line-height: rem(24px);
				}
				.forest-item1 {
					.img {
						background: url('../img/forest/map_location-2.jpg') no-repeat 50% 50%/cover;
					}
				}
				.forest-item2 {
					.img {
						background: url('../img/forest/map_location-1.jpg') no-repeat 50% 50%/cover;
					}
					
				}
				.forest-item3 {
					.img {
						background: url('../img/forest/map_location-3.jpg') no-repeat 50% 50%/cover;
					}
				}
				.owl-dots {
					top: calc(100vw * 290 / 575);
					text-align: center;
					position:absolute;
					margin:0 auto;
					width: 100%;
					.owl-dot {
						border-radius: 50%;
						background-color: rgb(0, 0, 0);
						opacity: 0.2;
						width: 12px;
						height: 12px;
					 	&.active {
							background-color: rgb(243, 115, 33);
							width: 32px;
							height: 12px;
							border-radius:20px;
							opacity: 1;
					 	}
					}
				}
			}
		}
	
	.forestResult {
		width:100vw;
		height:100%;
		padding:rem(30px) 0 rem(50px);
		background-color: rgb(255,255,255);
		z-index:31;
		background: url('../img/forest/bg-forest_result.jpg') 50% 50%/cover;
		.text {
			font-size: rem(14px);
			line-height: rem(23px);
			&.bold {
				padding-top: rem(40px);
				font-size: rem(17.5px);
				line-height: rem(25px);
			}
		}
	}
}
