//
// contribute
//
#contribute {
    @extend %section-top-margin-narrow;
    .wrapper > .section-header {
    	padding: rem(30px) 0 rem(100px);
    } 
    .chart {
	    position: relative;
    	background-color: #fff;
    	.mix-chart {
    		@extend %clearfix;
			background-color: $bg-gray;
			padding: rem(50px) 0;
			width:100vw;
			.chart-inner {
				.section-header {
					height:rem(80px);
					margin-bottom:rem(50px);
					.heading {
						color: $text-color-gray;
						font-size: rem(30px);
						font-weight: 700;
						margin-bottom: rem(20px);
						text-align: center;
					}
					.info-status li {
						padding-bottom: rem(5px);
						color: $text-color-gray;
						font-size:rem(18px);
						line-height:1.26;
						font-weight:300;
						span.kwh1 {
							background: url('data:img/png;base64,') no-repeat 50% 50%/contain;
							width: rem(20px);
							height: rem(20px);
							display: inline-block;
							margin-right: rem(10px);
						}
						span.kwh2 {
							background: url('data:img/png;base64,');
							width: rem(23px);
							height: rem(15px);
							display: inline-block;
							margin-right: rem(10px);
						}
					}

				}
        .chart-svg {
          background-image: url('data:img/png;base64,');
          background-position: 0% 50%;
          background-repeat: no-repeat;
          background-size: cover;
          position: relative;
          width: 80vw;
          height: calc(80vw * 304 / 633);
          svg {
            position: absolute;
            width:60vw;
            &#chart-bar {
              left: 12%;
              top: 24%;
            }
            &#point-over {
              left: 6.5%;
              top: -19%;
              opacity: 0;
              width:69vw;
              z-index:99;
            }
            &#chart-lines {
              left: 11.5%;
              top: -1.5%;
              width:59vw;
            }
            &#chart-point {
              width:57vw;
              left: 13.5%;
              top: -1%;
            }
          }
        }
	    	}
	    }
    	.pie-chart {
    		width: 100vw;
    		height:rem(520px);
    		background: url('../img/contribute/bg-chart-2.jpg') no-repeat 0% 50%/cover;
    		.chart-inner {
    			position: relative;
				.section-header {
					padding: rem(50px) 0 rem(20px);
					.heading {
						color: $text-color-white;
						font-size: rem(30px);
						font-weight: 700;
						margin-bottom: rem(20px);
						text-align: center;
					}
				}
    			.pie-svg {
    				position:relative;
					width:100vw;
					margin:0 auto;
					height: calc(65vw * 358 / 340);

		    		svg { 
		    			position: absolute;
						top:0;right:0;bottom:0;left:0;
						margin:0 auto;
						&#pie {
			    			width:65vw;
						}
						&#textDesc {
			    			width:70vw;
			    			margin: 2vw 0 0 4vw;
						}
		    		}

		    	}
		    	.text-ref {
		    		width:100%;
		    		padding: rem(30px) 0 0;
		    		text-align:center;
		    	}
	    	}
	    }
    	p.text {
    		padding: rem(40px) 0 rem(35px); 
    		line-height: rem(23.5px);
    	}
    }
    .info-graphic {
		background-color: $bg-gray;
		padding: rem(30px) 0 rem(100px);
		text-align:center;
		.section-header {
			padding:0;
		.heading-1 {
					.co2 {
						font-size: rem(26px);
	    			b {
	    				font-size: rem(15px);
	    			}
	    		}
		}
	}
	.text-ref {
      color: $text-color-black;
      text-align:center;
    }
	    .icon {
	    	padding:rem(20px) 0;
	    	margin:0 auto;
	    	svg {
	    		overflow:visible;
	    	}
	    	&.icon-tree {
	    		svg {
	    		width:40vw;
	    		}
	    	}
		    &.icon-space {
		    	svg {
		    	width:40vw;
				}
		    }
	    }
	    .sub-header {
	    	display:block;
	    	.title {
	    		font-size: rem(26px);
	    		font-weight: 600;
	    		color: $text-color-gray;
	    		padding:rem(20px) 0 rem(10px);

	    	}
	    	.text {
	    		color: $text-color-gray;
	    	}
	    }
    }
}